export default {
	data() {
		return {
			staticUrl: this.staticUrl,
			show : false,
			show2 : false
		}
	},
	methods:{
		// 第1个演示 开启与关闭
		showBanner : function(){
			this.show = true;
		},
		closeBanner : function(){
			this.show = false;
		},
		// 第2个演示 开启与关闭
		showBanner2 : function(){
			this.show2 = true;
		},
		closeBanner2 : function(){
			this.show2 = false;
		},
		tap2 : function(){
			uni.showToast({
				title:"您点击了红包图片",
				icon:"none"
			})
		}
	},
	components :{
		graceMaskView
	}
}
